<script setup>
import { ref, watch, nextTick, computed } from 'vue';
import { ShadowRoot } from 'vue-shadow-dom';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

// Funcs
import { useDarkMode } from './src/useDarkMode.js';
const { isDark } = useDarkMode();

// Comps
import Notifications from '@/components/utility/notifications/index.vue';
import LoadingIndicator from '@/components/utility/loading-indicator/index.ts';

// Refs
const adoptedStyleSheets = ref(new CSSStyleSheet());

/**
 * Injects styles into shadow root
 */
const setStyles = () => {
  const appStyleNode = document.querySelector('#uixpress-role-editor-css');
  if (!appStyleNode) {
    // Try to manually add stylesheet if not found
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.id = 'uixpress-role-editor-css';
    link.href = `${appStore.state.pluginBase}app/dist/assets/styles/role-editor.css`;
    document.head.appendChild(link);
    link.onload = () => {
      const appStyles = link.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
    return;
  }
  const appStyles = appStyleNode.sheet;
  for (const rule of [...appStyles.cssRules].reverse()) {
    adoptedStyleSheets.value.insertRule(rule.cssText);
  }
};

setStyles();
</script>

<template>
  <!--Menu -->
  <component is="style"> html{font-size:14px;}#wpcontent{padding:0}</component>
  <ShadowRoot
    tag="div"
    :adopted-style-sheets="[adoptedStyleSheets]"
    class="uipx-normalize"
  >
    <component is="style">
      :root{font-size:14px}#wpcontent{padding:0}</component
    >
    <div
      class="overflow-hidden uipx-normalize font-sans h-[var(--uixp-body-height)] max-h-[var(--uixp-body-height)]"
      :class="isDark ? 'dark' : ''"
    >
      <Notifications />
      <div class="translate-x-0">
        <LoadingIndicator :height="3" />
      </div>
      <RouterView />
    </div>
  </ShadowRoot>
</template>

<style>
html {
  --uixp-body-height: calc(100vh - var(--uixp-toolbar-height) - 1rem);
}
#wpbody-content {
  padding: 0;
}
#wpcontent {
  padding: 0 !important;
}
</style>

